import { NgModule } from '@angular/core';
import {AutoResizeTextarea} from "./auto-resize-textarea";
import {DropdownAddon} from "./dropdown-addon";
import {SpinnerNumber} from "./spinner-number";
import {TagsChips} from "./tags-chips";
import {TooltipAddon} from "./tooltip-addon";
import {DialogAddon} from "./dialog-addon";
import {TagsAutocomplete} from "./tags-autocomplete";
import {FitText} from "./fit-text";
import {ToggledClickThroughAddon} from "./toggled-click-through-addon";


@NgModule({
  declarations: [
    AutoResizeTextarea,
    DropdownAddon,
    SpinnerNumber,
    TagsChips,
    TooltipAddon,
    DialogAddon,
    TagsAutocomplete,
    FitText,
    ToggledClickThroughAddon
  ],
  exports: [
    AutoResizeTextarea,
    DropdownAddon,
    SpinnerNumber,
    TagsChips,
    TooltipAddon,
    DialogAddon,
    TagsAutocomplete,
    FitText,
    ToggledClickThroughAddon
  ]
})
export class SharedDirectives {}



