import { Injectable } from '@angular/core';
import { UUID } from "angular2-uuid";
import * as _ from "lodash";

declare var electron: any;

const COMMON_EVENT_NAME = 'ipc-promise-common-event';
const COMMON_REPEAT_EVENT_NAME = 'ipc-promise-common-repeat-event';
const UNREGISTER_COMMON_REPEAT_EVENT_NAME = 'ipc-promise-common-repeat-event-unregister';
const SUCCESS_EVENT_SUFFIX = '-success';
const FAILURE_EVENT_SUFFIX = '-failure';
const COMMON_SUCCESS_EVENT_NAME = COMMON_EVENT_NAME + SUCCESS_EVENT_SUFFIX;
const COMMON_FAILURE_EVENT_NAME = COMMON_EVENT_NAME + FAILURE_EVENT_SUFFIX;


@Injectable()
export class ipcWrapper {

  static counter = 0;

  /**
   * key: eventName + uuid
   * value: event listener
   * @type {Map<any, any>}
   */
  static listenersMap = new Map();

  static keyboardShortcuts:Map<string,any>=new Map<string,any>();

  constructor(){


  }

  static sendSystemCommand(systemCommand, ...args) {
    electron.ipcRenderer.send(systemCommand, args);
  }

  static popCallParams(id) : any {
    let params=ipcWrapper.map.get(id)
    ipcWrapper.map.delete(id);

    return params;
  }
  static map: Map<string,any> = new Map<string,any>();
  static sendIPC(eventName, ...data) {

    if (_.isArray(data) && data.length > 0) {
      data = _.without(data, undefined)
    }

    if (ipcWrapper.counter===0) {

      electron.ipcRenderer.on(COMMON_SUCCESS_EVENT_NAME, (event, resParams)=> {
        let callParams = ipcWrapper.popCallParams(resParams.id);
        if (callParams) {
          callParams.resolve(resParams.data);
        }
      });

      electron.ipcRenderer.on(COMMON_FAILURE_EVENT_NAME, (event, resParams) => {
        let callParams =ipcWrapper.popCallParams(resParams.id);
        if (callParams) {
          callParams.reject(resParams.data);
        }
      });
    }

    return new Promise(function (resolve, reject) {
      let id = eventName + new Date().getTime() + ++ipcWrapper.counter;

      let params = {
        data: data,
        eventName: eventName,
        id: id,
        resolve: resolve,
        reject: reject
      };

      ipcWrapper.map.set(id,params);

      electron.ipcRenderer.send(COMMON_EVENT_NAME, params);
    });
  }

  static registerForNotification(eventName, callback, ... data) {

    let uuid = UUID.UUID();

    let handleEvent = (event, params) => {
      if (callback) {
        callback(...params.data);
      }
    };

    electron.ipcRenderer.on(COMMON_REPEAT_EVENT_NAME + '_' + eventName, handleEvent);

    ipcWrapper.listenersMap.set(eventName + uuid, handleEvent);

    let params = {
      data: data,
      eventName: eventName
    }

    electron.ipcRenderer.send(COMMON_REPEAT_EVENT_NAME, params);

    return uuid;
  }

  // stop getting notifications for the channel named COMMON_REPEAT_EVENT_NAME + '_' + eventName
  static unregisterForNotification(eventName, id = '') {
    //remove specific listener from channel
    let listenerKey = eventName + id;
    if(ipcWrapper.listenersMap.has(listenerKey)) {
      electron.ipcRenderer.removeListener(COMMON_REPEAT_EVENT_NAME + '_' + eventName, ipcWrapper.listenersMap.get(listenerKey));
      ipcWrapper.listenersMap.delete(listenerKey);
    } else {
      electron.ipcRenderer.removeAllListeners(COMMON_REPEAT_EVENT_NAME + '_' + eventName);
      ipcWrapper.clearListenersMapByEventName(eventName);
    }
  }

  private static clearListenersMapByEventName(eventName) {
    _.forEach(
      _.filter([...ipcWrapper.listenersMap.keys()], (mapKey) => { return mapKey.indexOf(eventName) > -1 }), (rmKey) => {
        ipcWrapper.listenersMap.delete(rmKey);
      });
  }

  private static registerShortcutCB(shortcutData:any) {
    let shortcut=shortcutData['shortcut'];
    let cb=ipcWrapper.keyboardShortcuts.get(shortcut);
    if (cb) {
      cb();
    }
  }

  static registerShortcut(shortcut:string,cb:any){

    //we need to register to the callback only once!
    let callback = ipcWrapper.keyboardShortcuts.size>0 ? null : ipcWrapper.registerShortcutCB;
    ipcWrapper.keyboardShortcuts.set(shortcut,cb);
    return ipcWrapper.registerForNotification("registerGlobalShortcut",callback,shortcut)
  }

  static unRegisterShortcut(shortcut:string, id = '')
  {
    ipcWrapper.keyboardShortcuts.delete(shortcut);
    ipcWrapper.unregisterForNotification("registerGlobalShortcut", id);
  }
}



