import {ModuleWithProviders}  from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ClassroomCaptureComponent } from './classroom-capture/components/default-app-view/cc.component';

import { AppInfoComponent } from './app-info/components/app-info.component'
import { AppSettingsComponent } from './app-settings/components/app-settings/app-settings.component'
import {SchedulingComponent} from "./scheduling/components/scheduling.component";
import {GeneralSettingsComponent} from "./app-settings/components/general-settings/general-settings.component";
import {GeneralSettingsComponentGuard} from "./app-settings/guards/general-settings-component-guard";

const appRoutes: Routes = [
    { path: 'info', component: AppInfoComponent },
    { path: 'scheduling', component: SchedulingComponent },
  { path: 'settings', component: AppSettingsComponent, children: [
    { path: '', redirectTo: 'general-settings', pathMatch: 'full'},
    { path: 'general-settings', component: GeneralSettingsComponent, canDeactivate: [GeneralSettingsComponentGuard]}
  ]},
  { path: '', component: ClassroomCaptureComponent, pathMatch: 'full'} // redirect to home page on load
];

export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);



