import {Injectable} from "@angular/core";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import * as _ from "lodash";
import {RecordingDetailsService} from "./recording-details.service";
import {AppNotificationsService} from "../infra/app-notifications.service";
import {AppStateService} from "../infra/app-state.service";
import {AppConfigService} from "../infra/app-config.service";
import {VideoDeviceService} from "../capture-device/video-device.service";

@Injectable()
export class SlideService {

  constructor(private appState: AppStateService,
              private appConfig: AppConfigService,
              private recordingDetails: RecordingDetailsService,
              private notificationService: AppNotificationsService,
              private videoService: VideoDeviceService)
  {
    this.registerForSlideCapturingNotifications();
  }

  public shouldCaptureSlides(): Promise<any>
  {
    let screenSession = _.find(this.appState.getActiveRecordableSessionsIds(), (sessionId) => {
      return this.videoService.getCaptureDeviceTypeBySessionId(sessionId) === 'screen';
    });
    return this.appConfig.setConfigData('config.captureEngine.capturePowerPoint', //config path
      this.appConfig.getConfigData('config.captureEngine.capturePowerPoint') && !_.isEmpty(screenSession), //new captureSlide value
      true, //reload engine with new slide capture value
      false //don't save this dynamic value to disc (of even save to app config object) - only current engine action will use this new value
    );
  }

  private registerForSlideCapturingNotifications()
  {
    ipcWrapper.registerForNotification('capture-ppt', (data: any) => {
      if (_.isString(data)) {
        data = JSON.parse(data)
      }
      this.recordingDetails.addSlideCapturedFilePath(data);
    });

    ipcWrapper.registerForNotification('capture-ppt-error', (data: any) => {
      if (_.isString(data)) {
        data = JSON.parse(data)
        if (data.code !== "EngineError_UnspecifiedError") // no need to show this to the user as he has nothing to do
          this.notificationService.sendNotification('NOTIFICATIONS.' + data.code, "error");
      }
    });
  }

}



