import {Injectable} from '@angular/core';
import {SelectItem} from "primeng/components/common/api";
import * as _ from "lodash";


let errorCodes= {
  'INVALID_WIDGET_ID': 'NOTIFICATIONS.error_invalid_partner_id',
  "SYSTEM_NAME_ALREADY_EXISTS": 'SETTINGS.SYSTEM_NAME_ALREADY_EXISTS'
}

export type promiseState = {
  status: string,
  value?: any,
  reason?: any
}


@Injectable()
export class utils {

  static STRING_LIMIT = 28;

  constructor() {
  }

  static sortResolutionArray(resArray: SelectItem[]) {
    resArray.sort((a, b) => {
      return b.value[0] * b.value[1] - a.value[0] * a.value[1];
    });
  }

  static getAPIErrorMessage(err:any) {
    let error = 'NOTIFICATIONS.error_server_not_reachable';

    if (err && _.isObject(err.error) && err.error.code){


      error = errorCodes[err.error.code]

      if (!error) {
        error = err.error.message;
      }
    }

    return error;

  }
  static stringifyMsg(msg) {
    if (_.isError(msg)) {
      return msg.message + " " + msg.stack;
    }
    if (_.isString(msg))
      return msg;

    return JSON.stringify(msg)
  }

  static delayPromise(milis) {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve();
      }, milis);
    })
  }

  static colorspace_weight = {
    'NV12': 100,
    'YV12': 99,
    'YUY2': 98,
    'UYVY': 97
  };

  static filterAndSortInputs(inputTypes: any, limits) {
    let ret = _.filter(_.castArray(inputTypes), (input) => {
      let fps = input['fps'];
      return  (fps >= limits.minFPS &&  fps <= limits.maxFPS  );
    });


    ret = _.orderBy(ret, [
        (input) => {
          if (limits.defaultHeight) {
            return Math.abs(input['height'] - limits.defaultHeight);
          } else {
            return 1; //ignore height
          }
        },
        (input) => {
          return Math.abs(input['fps'] - limits.defaultFPS);
        },
        function(input) {
          return input.interlaceMode==='progressive' ? 1 : 0;
        },
        function(input) {
          return input['width']*input['height']
        },
        (input) => {
          let weight = utils.colorspace_weight[input['colorspace'].toUpperCase()]
          if (weight) {
            return weight;
          }
          return 0;

        }],
      ["asec", "asec", "desc","desc", "desc"]);

    return ret;
  }


  static sha1(msg) {
    function rotate_left(n, s) {
      var t4 = ( n << s ) | (n >>> (32 - s));
      return t4;
    };
    function lsb_hex(val) {
      var str = "";
      var i;
      var vh;
      var vl;
      for (i = 0; i <= 6; i += 2) {
        vh = (val >>> (i * 4 + 4)) & 0x0f;
        vl = (val >>> (i * 4)) & 0x0f;
        str += vh.toString(16) + vl.toString(16);
      }
      return str;
    };
    function cvt_hex(val) {
      var str = "";
      var i;
      var v;
      for (i = 7; i >= 0; i--) {
        v = (val >>> (i * 4)) & 0x0f;
        str += v.toString(16);
      }
      return str;
    };
    function Utf8Encode(string) {
      string = string.replace(/\r\n/g, "\n");
      var utftext = "";
      for (var n = 0; n < string.length; n++) {
        var c = string.charCodeAt(n);
        if (c < 128) {
          utftext += String.fromCharCode(c);
        }
        else if ((c > 127) && (c < 2048)) {
          utftext += String.fromCharCode((c >> 6) | 192);
          utftext += String.fromCharCode((c & 63) | 128);
        }
        else {
          utftext += String.fromCharCode((c >> 12) | 224);
          utftext += String.fromCharCode(((c >> 6) & 63) | 128);
          utftext += String.fromCharCode((c & 63) | 128);
        }
      }
      return utftext;
    };
    var blockstart;
    var i, j;
    var W = new Array(80);
    var H0 = 0x67452301;
    var H1 = 0xEFCDAB89;
    var H2 = 0x98BADCFE;
    var H3 = 0x10325476;
    var H4 = 0xC3D2E1F0;
    var A, B, C, D, E;
    var temp;
    msg = Utf8Encode(msg);
    var msg_len = msg.length;
    var word_array = new Array();
    for (i = 0; i < msg_len - 3; i += 4) {
      j = msg.charCodeAt(i) << 24 | msg.charCodeAt(i + 1) << 16 |
        msg.charCodeAt(i + 2) << 8 | msg.charCodeAt(i + 3);
      word_array.push(j);
    }
    switch (msg_len % 4) {
      case 0:
        i = 0x080000000;
        break;
      case 1:
        i = msg.charCodeAt(msg_len - 1) << 24 | 0x0800000;
        break;
      case 2:
        i = msg.charCodeAt(msg_len - 2) << 24 | msg.charCodeAt(msg_len - 1) << 16 | 0x08000;
        break;
      case 3:
        i = msg.charCodeAt(msg_len - 3) << 24 | msg.charCodeAt(msg_len - 2) << 16 | msg.charCodeAt(msg_len - 1) << 8 | 0x80;
        break;
    }
    word_array.push(i);
    while ((word_array.length % 16) != 14) word_array.push(0);
    word_array.push(msg_len >>> 29);
    word_array.push((msg_len << 3) & 0x0ffffffff);
    for (blockstart = 0; blockstart < word_array.length; blockstart += 16) {
      for (i = 0; i < 16; i++) W[i] = word_array[blockstart + i];
      for (i = 16; i <= 79; i++) W[i] = rotate_left(W[i - 3] ^ W[i - 8] ^ W[i - 14] ^ W[i - 16], 1);
      A = H0;
      B = H1;
      C = H2;
      D = H3;
      E = H4;
      for (i = 0; i <= 19; i++) {
        temp = (rotate_left(A, 5) + ((B & C) | (~B & D)) + E + W[i] + 0x5A827999) & 0x0ffffffff;
        E = D;
        D = C;
        C = rotate_left(B, 30);
        B = A;
        A = temp;
      }
      for (i = 20; i <= 39; i++) {
        temp = (rotate_left(A, 5) + (B ^ C ^ D) + E + W[i] + 0x6ED9EBA1) & 0x0ffffffff;
        E = D;
        D = C;
        C = rotate_left(B, 30);
        B = A;
        A = temp;
      }
      for (i = 40; i <= 59; i++) {
        temp = (rotate_left(A, 5) + ((B & C) | (B & D) | (C & D)) + E + W[i] + 0x8F1BBCDC) & 0x0ffffffff;
        E = D;
        D = C;
        C = rotate_left(B, 30);
        B = A;
        A = temp;
      }
      for (i = 60; i <= 79; i++) {
        temp = (rotate_left(A, 5) + (B ^ C ^ D) + E + W[i] + 0xCA62C1D6) & 0x0ffffffff;
        E = D;
        D = C;
        C = rotate_left(B, 30);
        B = A;
        A = temp;
      }
      H0 = (H0 + A) & 0x0ffffffff;
      H1 = (H1 + B) & 0x0ffffffff;
      H2 = (H2 + C) & 0x0ffffffff;
      H3 = (H3 + D) & 0x0ffffffff;
      H4 = (H4 + E) & 0x0ffffffff;
    }
    let str_temp: string = cvt_hex(H0) + cvt_hex(H1) + cvt_hex(H2) + cvt_hex(H3) + cvt_hex(H4);

    return str_temp.toLowerCase();
  };

  public static areEventsEqual(eventA, eventB) {
    // if both not exits => equal
    // if both exits with the same properties => equal
    return ((_.isEmpty(eventA) && _.isEmpty(eventB))
    || (!_.isEmpty(eventA)
    && !_.isEmpty(eventB)
    && eventA['id'] === eventB['id']
    && eventA['startDate'] === eventB['startDate']
    && eventA['endDate'] === eventB['endDate']
    && eventA['summary'] === eventB['summary']
    && eventA['description'] === eventB['description']
    && eventA['organizer'] === eventB['organizer']
    && eventA['tags'] === eventB['tags']));
  }

  /**
   * Concat url and prevent double slash and redundant spaces - no handling for params.
   * @param input: a collected array of url parts to be concat.
   */
  static urlBuilder(...input) {
    let url;

    if (input.length && input[0].startsWith('file:')) {
      // join all except the file string with /
      url = [].slice.call(input, 1).join('/');

      // remove double splash
      url = url.replace(/([^:\s])\/+/g, '$1/');

      // add the file:///
      url = [input[0], url].join('/');

      // make sure max / is 3
      return url.replace(/(\/{0,3})\/*/g, '$1');
    }
    else {
      // join all with /
      url = [].slice.call(input, 0).join('/');

      // remove double splash
      return url.replace(/([^:\s])\/+/g, '$1/');
    }
  }

  /**
   * sort inputs names alphabetically when audio input is the last one
   * f.e: ['session0', 'session1'..., 'audio']
   * @param inputsNames
   */
  public static sortInputsNames(inputsNames)
  {
    inputsNames.sort((a, b) => {
      //audio should be last
      if(a === 'audio') return 1;
      if(b === 'audio') return -1;
      //alphabetical order
      return a < b ? -1 : 1;
    });
    return inputsNames;
  }

  /**
   * each sessionName has it's own display name
   * After classroom app multiple inputs impl. our pc and cc app started to use different sessions display name...
   * returned string are translation placeholders under COMMON category (f.e COMMON.primary in en.json locale)
   */
  public static getDisplayNameForSessionName(appType, defaultPrimarySession, sessionName)
  {
    if(appType === 'cc') {
      switch (sessionName) {
        case 'audio':
          return 'audio';
        case 'session0':
          return 'primary_input';
        default:
          let sessionIndex = parseInt(sessionName.split('session')[1]);
          return 'input' + ++sessionIndex;
      }
    } else {
      return sessionName === 'audio' ? 'audio' : (sessionName === defaultPrimarySession ? 'primary' : 'secondary');
    }
  }

  /**
   *
   * implementing Promise.allSettled behaviour (currently a stage 3 proposal: https://github.com/tc39/proposal-promise-allSettled)
   *
   * Returning an array of completed promises, some can be rejected and some fulfilled
   * Usage example after returned:
   *    const successfulPromises = results.filter(p => p.status === 'fulfilled');
   * @param promises array of promises
   * @return an array of promiseState objects
   */

  public static promiseAllSettled(promises: Promise<any>[]): Promise<promiseState[]>
  {
    function reflect(promise) {
      return promise.then(
        (v) => {
          return <promiseState>{ status: 'fulfilled', value: v };
        },
        (error) => {
          return <promiseState>{ status: 'rejected', reason: error };
        }
      );
    }
    return Promise.all(promises.map(reflect));
  }

}



