import {Component, Input, Output, EventEmitter} from '@angular/core';

@Component({
  selector: 'add-visual-input',
  templateUrl: './add-visual-input.component.html',
  styleUrls:  ['./add-visual-input.component.scss'],
  providers: []
})
export class AddVisualInputComponent {

  @Input() shouldDisable: boolean = true;
  @Output() inputAdded: EventEmitter<void> = new EventEmitter<void>();

  private disableButton = false;

  onAddInputClick()
  {
    if(!this.disableButton) { //prevent double click
      this.disableButton = true;
      this.inputAdded.emit();
      setTimeout(() => {
        this.disableButton = false;
      }, 300);
    }
  }

  isAddButtonDisable()
  {
    return this.shouldDisable || this.disableButton;
  }
}



