import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';
import {SystemDataService} from "../services/infra/system-data.service";

@Pipe({
  name: 'time'
})
export class TimePipe implements PipeTransform {

  constructor(private systemData : SystemDataService) {
  }

  transform(seconds: number, format: string, suffix: string): string
  {
    let time = moment(seconds).locale(this.systemData.getLang()).format(format);

    if (suffix === 'REMOVE_AM_PM_SUFFIX'){
      time = time.split(' ')[0];
    }
    else if (suffix === 'REMOVE_SPACES'){
      time = time.replace(' ', '').toLowerCase();
    }

    return time;
  }
}



