import {Component, HostListener, OnInit, Input} from '@angular/core';
import {TranslateService} from 'ng2-translate';
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {Router} from "@angular/router";
import {AppLoggerService} from "../../services/infra/app-logger.service";
import {AppStateService} from "../../services/infra/app-state.service";
import {PanelsService} from "../../services/infra/panels.service";
import {AccessibilityService} from "../../services/infra/accessability.service";
import {utils} from "../../../utils/utils";


export declare type AppContainerProperties = {
  header: {
    logoIcon: string,
    buttons: string[],
    extendedButtons?: any[]
  }
  navbar: {
    links: NavBarLink[]
  },
  showBackLight?: boolean
}

export declare type NavBarLink = {
  linkContainer?: string,
  linkClass: string,
  label: string,
  routerLink: string,
  shouldRender?: boolean | Function,
  shouldDisableWhenNotStable?: boolean
}


@Component({
  selector: 'app-container',
  templateUrl: './app-container.component.html',
  styleUrls: ['./app-container.component.scss']
})
export class AppContainerComponent implements OnInit {

  debugState: boolean = false;
  private lastElement: any;
  private debug_mode_UUID = '';
  @Input() containerProperties;

  constructor(translate: TranslateService,
              private log: AppLoggerService,
              private appState: AppStateService,
              private panelsService: PanelsService,
              private router: Router)
  {}

  ngOnInit() {
    this.debug_mode_UUID = ipcWrapper.registerForNotification('debug_mode', (data : boolean) => {
      this.debugState = data;
    });
  }

  ngOnDestroy() {
    ipcWrapper.unregisterForNotification('debug_mode', this.debug_mode_UUID);
  }

  onMinimizeToTrayClick()
  {
    return ipcWrapper.sendSystemCommand('closeApp'); // go to tray
  }

  onMinimizeClick()
  {
    ipcWrapper.sendSystemCommand('minimize');
  }

  onMaximizeRestore()
  {
    ipcWrapper.sendSystemCommand('maximize-restore');
  }

  isMenuDisabled()
  {
    return !this.appState.isAppStable();
  }

  shouldRender(value = true){
    return (typeof value === 'boolean') ? value : value();
  }

  getLinksAriaLabel(link)
  {
    return link.ariaLabel ? link.ariaLabel : link.label;
  }

  getExtendedButtonsAriaLabel(button)
  {
    return button.ariaLabel ? button.ariaLabel : button.title;
  }

  navigateToLink(link, disableIfUnstable)
  {
    if(disableIfUnstable && this.isMenuDisabled()) {
      return;
    }

    this.router.navigateByUrl(link)
      .catch((err) => {
        this.log.error('Failed to navigate to: ' + link + '. ' + utils.stringifyMsg(err))
      });
  }
}



