import {Directive, OnDestroy, HostListener, Self, ElementRef} from "@angular/core";
import {ipcWrapper} from "../../utils/ipcWrapper";


@Directive({
  selector: '[toggled-click-through-addon]',
})
export class ToggledClickThroughAddon implements OnDestroy {

  constructor(private elementRef: ElementRef) {
  }

  @HostListener('mouseover', ['$event.target']) onMouseOver(target) {
    if (this.elementRef.nativeElement.className === target.className) {
      ipcWrapper.sendIPC("setIgnoreMouseEvents",true);
    }
  }

  @HostListener('mouseout', ['$event.target']) onMouseOut(target) {
    ipcWrapper.sendIPC("setIgnoreMouseEvents",false);
  }

  ngOnDestroy(): void {
    ipcWrapper.sendIPC("setIgnoreMouseEvents",false);
  }

}



