import {Injectable} from "@angular/core";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import * as _ from "lodash";
import {AppConfigService} from "../infra/app-config.service";
import {AppLoggerService} from "../infra/app-logger.service";
import {VideoDeviceSettingsService} from "../capture-device/video-device-settings.service";

@Injectable()
export class MouseClickIndicationService {

  constructor(private appConfig: AppConfigService,
              private videoDeviceSettingsService: VideoDeviceSettingsService,
              private log: AppLoggerService) {}

  public startMouseClickIndication()
  {
    if(this.appConfig.getConfigData('config.captureApp.recording.mouseClickIndication') === true) {
      let displaysBounds = [];
      _.forEach(this.videoDeviceSettingsService.getAllActiveDevicesObjects(), device => {
        if(device['bounds']) displaysBounds.push(device['bounds']);
      });
      if(_.isEmpty(displaysBounds)) {
        this.log.info('there are no screens for current recording, mouse click indication is not needed');
        return Promise.resolve({});
      } else {
        return ipcWrapper.sendIPC('create-mouse-click-indication-windows', displaysBounds);
      }
    } else {
      this.log.info('There is no need in mouse click indication for current recording');
      return Promise.resolve({});
    }
  }

  public stopMouseClickIndication(){
    return ipcWrapper.sendIPC('destroy-mouse-click-indication-windows');
  }
}



