import { Injectable } from '@angular/core';
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {AppLoggerService} from "./app-logger.service";
import {utils} from  "../../../utils/utils";

@Injectable()
export class SystemDataService {

  private systemData : any = null;

  constructor(private log: AppLoggerService)
  {
    ipcWrapper.sendIPC('getLocalizationData').then((data : any) => {
      this.systemData = data;
    }).catch((err) => {
      this.log.error('Failed to getLocalizationData. ' + utils.stringifyMsg(err));
    });
  }

  public getLang() : string {
    if (this.systemData && this.systemData['lang']){
      return this.systemData['lang'];
    }
    return navigator.language;
  }
}



