import {NgModule} from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import {HttpModule} from '@angular/http';
import {TranslateModule} from 'ng2-translate';

//shared modules
import {SharedPipes} from "../shared/pipes/shared-pipes.module";
import {SharedDirectives} from "../shared/directives/shared-directives.module";
import {InfraModule} from "../shared/services/infra/infra.module";
import {MonitoringModule} from "../shared/services/monitoring/monitoring.module";
import {ResourceIdModule} from "../shared/services/resource-id/resource-id.module";
import {CaptureDeviceModule} from "../shared/services/capture-device/capture-device.module";
import {RecordingModule} from "../shared/services/recording/recording.module";
import {LogCollectorModule} from "../shared/components/log-collector/log-collector.module";
import {LiveModule} from "../shared/services/live/live.module";

//main application components
import {AppComponent} from "./app.component";
import { AppInfoComponent } from "./app-info/components/app-info.component";
import { ClassroomCaptureComponent }  from './classroom-capture/components/default-app-view/cc.component';
import {SchedulingComponent} from "./scheduling/components/scheduling.component";

//app routing
import { routing }  from './app.routing';

//components
import {RecordingDetailsComponent} from "./classroom-capture/components/default-app-view/recording-details.component";
import {RecordingFinalizeComponent} from "./classroom-capture/components/default-app-view/recording-finalize.component";
import {AppSettingsComponent} from "./app-settings/components/app-settings/app-settings.component";
import {RecordingNotificationsComponent} from "./classroom-capture/components/recording-notifications/recording-notifications.component";

//services
import {MinimizeService} from "../utils/minimize.service";
import {RecordingManagerService} from "./classroom-capture/services/recording-manager.service";
import {SchedulingService} from "./classroom-capture/services/scheduling.service";
import {SettingsService} from "./app-settings/services/settings.service";

//UI components
import {CheckboxModule, DropdownModule, DialogModule, ChipsModule, ConfirmDialogModule, AutoCompleteModule,SpinnerModule} from 'primeng/primeng';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {TooltipModule} from "primeng/components/tooltip/tooltip";
import {AppContainerComponent} from "../shared/components/app-container/app-container.component";
import {UserLoginComponent} from "../shared/components/user-login/user-login.component";
import {GeneralSettingsComponent} from "./app-settings/components/general-settings/general-settings.component";
import {ProgressBarModule} from "primeng/components/progressbar/progressbar";
import {ConfirmationService} from "primeng/components/common/confirmationservice";
import {GeneralSettingsComponentGuard} from "./app-settings/guards/general-settings-component-guard";
import {InputsManagementsComponent} from "./classroom-capture/components/inputs-management/inputs-management.component";
import {InputsManagementService} from "./classroom-capture/components/inputs-management/inputs-management.service";
import {AddVisualInputComponent} from "./classroom-capture/components/inputs-management/add-visual-input/add-visual-input.component";
import {AudioInputComponent} from "./classroom-capture/components/inputs-management/audio-input/audio-input.component";
import {VisualInputsContainerComponent} from "./classroom-capture/components/inputs-management/visual-inputs-container/visual-inputs-container.component";
import {VisualInputComponent} from "./classroom-capture/components/inputs-management/visual-inputs-container/visual-input/visual-input.component";
import {VisualInputSettingsComponent} from "./classroom-capture/components/inputs-management/visual-inputs-container/visual-input-settings/visual-input-settings.component";
import {VisualInputControllersComponent} from "./classroom-capture/components/inputs-management/visual-inputs-container/visual-input-controllers/visual-input-controllers.component";
import {AudioSettingsComponent} from "./classroom-capture/components/inputs-management/audio-input-settings/audio-settings.component";

@NgModule({
  imports:
    [
      SharedPipes,
      SharedDirectives,
      InfraModule,
      ResourceIdModule,
      MonitoringModule,
      CaptureDeviceModule,
      RecordingModule,
      BrowserModule,
      HttpModule,
      TranslateModule.forRoot(),
      routing,
      FormsModule,
      CheckboxModule,
      DropdownModule,
      DialogModule,
      ChipsModule,
      ConfirmDialogModule,
      BrowserAnimationsModule,
      AutoCompleteModule,
      TooltipModule,
      SpinnerModule,
      ProgressBarModule,
      LogCollectorModule,
      LiveModule
    ],
  declarations:
    [
      AppComponent,
      AppContainerComponent,
      AppInfoComponent,
      AppSettingsComponent,
      GeneralSettingsComponent,
      SchedulingComponent,
      ClassroomCaptureComponent,
      RecordingDetailsComponent,
      RecordingFinalizeComponent,
      RecordingNotificationsComponent,
      UserLoginComponent,

      InputsManagementsComponent,
      AddVisualInputComponent,
      AudioInputComponent,
      VisualInputsContainerComponent,
      VisualInputComponent,
      VisualInputControllersComponent,
      VisualInputSettingsComponent,
      AudioSettingsComponent
    ],
  providers:
    [
      MinimizeService,
      RecordingManagerService,
      SchedulingService,
      SettingsService,
      ConfirmationService,
      InputsManagementService,
      GeneralSettingsComponentGuard
    ],
  bootstrap: [ AppComponent ]
})
export class AppModule { }



