import {Injectable} from "@angular/core";
import { UUID } from "angular2-uuid";
import {TranslateService} from "ng2-translate";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {AppLoggerService} from "../../services/infra/app-logger.service";
import {AppConfigService} from "../../services/infra/app-config.service";
import {ConfirmationService} from "primeng/components/common/confirmationservice";
import {AppNotificationsService} from "../../services/infra/app-notifications.service";

@Injectable()
export class LogCollectorService {

  readonly COLLECTOR_STATUS = {
    IN_PROGRESS: 'IN_PROGRESS',
    SUCCEEDED: 'SUCCEEDED',
    FAILED: 'FAILED'
  };

  private logUploadProgress = 0;
  private fakeProgressInterval = null;
  private currentLogCollectorId = null;
  private logCollectorStatusCheckInterval = null;
  private uploadingLogs = false;


  constructor(private log: AppLoggerService,
              private appConfig: AppConfigService,
              private translate: TranslateService,
              private confirmationService: ConfirmationService,
              private appNotifications: AppNotificationsService)
  {

  }

  public getFakeLogUploadProgress()
  {
    return this.logUploadProgress
  }

  public resetFakeLogUploadProgress()
  {
    this.logUploadProgress = 0;
  }

  public onLogClick()
  {
    this.uploadingLogs = true;
    this.currentLogCollectorId = UUID.UUID();
    this.startFakeLogsUploadProgress();
    ipcWrapper.sendIPC('collect-logs-and-data', this.currentLogCollectorId);
    this.startCollectorStatusCheckInterval();
  }

  public isUploadingLogs()
  {
    return this.uploadingLogs;
  }

  public openLogsDir()
  {
    ipcWrapper.sendIPC('openDirectory', this.appConfig.getConfigData('config.shared.logsDir'))
      .then(success => {
        if(!success) this.log.error('Failed to open logs folder');
      });
  }

  public clearCollectorStatusCheckInterval()
  {
    this.uploadingLogs = false;
    clearInterval(this.logCollectorStatusCheckInterval);
  }

  askBeforeLeave() {
    return new Promise((resolve, reject) => {
      if (this.isUploadingLogs()) {
        this.translate.get('SETTINGS.uploading_logs_leave_while_uploading')
          .subscribe((leaveWithoutChangesText: string) => {
            this.confirmationService.confirm({
              message: leaveWithoutChangesText,
              key: "leave-while-uploading-logs",
              accept: () => {
                this.clearCollectorStatusCheckInterval();
                //since the next call will be to route to different page, there is an issue with primeng confirm dialog UI. we need to wait for it to close...
                setTimeout(() => {
                  resolve(true);
                }, 1000);
              },
              reject: () => {
                resolve(false);
              }
            });
          });
      } else {
        resolve(true);
      }
    });
  }

  private openUploadedLogsTokenPopup(tokenId)
  {
    this.translate.get('SETTINGS.uploaded_logs_token_message')
      .subscribe((text: string) => {
        this.confirmationService.confirm({
          message: text + "<br><br><span class='log-token'>" + tokenId + "</span>",
          key: "uploaded-logs-token-msg"
        });
      });
  }

  private startFakeLogsUploadProgress()
  {
    this.logUploadProgress = 4;
    this.fakeProgressInterval = setInterval(() => {
      let operand = this.logUploadProgress > 70 ? 1 : 7;
      this.logUploadProgress += operand;
      if(this.logUploadProgress > 96) this.clearFakeLogsUploadInterval();
    }, 4000);
  }

  private startCollectorStatusCheckInterval()
  {
    this.logCollectorStatusCheckInterval = setInterval(() => {
      ipcWrapper.sendIPC('get-log-collector-status', this.currentLogCollectorId)
        .then((collector) => {
          if(collector && collector['status'] !== this.COLLECTOR_STATUS.IN_PROGRESS) {
            this.clearFakeLogsUploadInterval();
            if(collector['status'] === this.COLLECTOR_STATUS.SUCCEEDED) {
              this.logUploadProgress = 100;
              setTimeout(() => {
                this.openUploadedLogsTokenPopup(collector['collectionUrl']);
                this.clearCollectorStatusCheckInterval();
              }, 1000); //so the user will see the progress bar at 100%
            } else {
              this.resetFakeLogUploadProgress();
              this.clearCollectorStatusCheckInterval();
              this.appNotifications.sendNotification('SETTINGS.upload_logs_failure', 'warn');
            }
          }
        });
    }, 5000);
  }

  private clearFakeLogsUploadInterval()
  {
    clearInterval(this.fakeProgressInterval);
  }



}



