import {Component} from '@angular/core';
import {AppLoggerService} from "../../../../shared/services/infra/app-logger.service";
import {InputsManagementService} from "./inputs-management.service";
import {utils} from "../../../../utils/utils";
import {AppConfigService} from "../../../../shared/services/infra/app-config.service";
import {PanelsService} from "../../../../shared/services/infra/panels.service";

@Component({
  selector: 'inputs-management',
  templateUrl: './inputs-management.component.html',
  styleUrls:  ['./inputs-management.component.scss'],
  providers: []
})
export class InputsManagementsComponent {

  constructor(
    private log: AppLoggerService,
    private appConfig: AppConfigService,
    private panelsService: PanelsService,
    private inputsManagementService: InputsManagementService)
  {

  }

  ngOnInit()
  {
    //for first component load to avoid racing
    this.inputsManagementService.setActiveInputs();
    this.inputsManagementService.subscribeToSessionsInitEvent();
  }

  ngOnDestroy()
  {
    this.inputsManagementService.unSubscribeFromSessionsInitEvent();
  }

  onInputAddedEvent()
  {
    this.inputsManagementService.addNewSession()
      .catch((err) => {
        this.log.error('InputsManagementsComponent Failed to add new session: ' + utils.stringifyMsg(err));
        //todo [SA] show the user notification
      })
  }

  allowAddInput()
  {
    let maxNumOfInputs = this.appConfig.getConfigData('config.captureApp.maxNumberOfInputs');
    return this.inputsManagementService.isAppStable() &&
      this.inputsManagementService.getActiveVisualInputsCount() < maxNumOfInputs &&
      !this.panelsService.isCurrentPanelActive();
  }

  getActiveInputs()
  {
    return this.inputsManagementService.activeVisualInputs;
  }

}



