import {Component, Input, ViewEncapsulation} from "@angular/core";

@Component({
  selector: 'management-confirm-dialog',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './management-confirm-dialog.component.html',
  styleUrls:  ['./management-confirm-dialog.component.scss']
})
export class ManagementConfirmDialogComponent {

  readonly BUTTON_SIZE = {
    small: 'small',
    large: 'large'
  };

  // to open multiple confirm dialogs we need to supply an id when calling with confirmationService
  @Input() confirm_dialog_key: string;
  @Input() reject_button_text: string;
  @Input() accept_button_text: string;
  @Input() accept_button_size: string;
  @Input() personalize_class: string
}



