import { NgModule } from '@angular/core';
import {AudioDeviceService} from "./audio-device.service";
import {AudioDeviceSettingsService} from "./audio-device-settings.service";
import {DeviceConnectionService} from "./device-connection.service";
import {DevicePreviewService} from "./device-preview.service";
import {DeviceRecoveryService} from "./device-recovery.service";
import {DeviceResourcesService} from "./device-resources.service";
import {VideoDeviceService} from "./video-device.service";
import {VideoDeviceSettingsService} from "./video-device-settings.service";
import {InfraModule} from "shared/services/infra/infra.module";
import {MonitoringModule} from "../monitoring/monitoring.module";
import {DefaultSettingsService} from "./default-settings.service";
import {LiveModule} from "shared/services/live/live.module";


@NgModule({
  imports: [
    InfraModule,
    MonitoringModule,
    LiveModule
  ],
  providers: [
    AudioDeviceService,
    AudioDeviceSettingsService,
    DeviceConnectionService,
    DevicePreviewService,
    DeviceRecoveryService,
    DeviceResourcesService,
    VideoDeviceService,
    VideoDeviceSettingsService,
    DefaultSettingsService
  ]
})
export class CaptureDeviceModule {}



