import {Injectable} from "@angular/core";
import {Observable} from "rxjs";

@Injectable()
export class MinimizeService {
  private minimizeRecording$ = null;

  public minimizeToTray(delaySec: number){
    this.cancelTrayMinimizationSubscription();
    this.minimizeRecording$ = Observable.timer(delaySec * 1000).subscribe(() => {
      window.close();
    });
  }

  public cancelTrayMinimizationSubscription(){
    if (this.minimizeRecording$){
      this.minimizeRecording$.unsubscribe();
    }
  }
}



