import {Injectable} from "@angular/core";
import {AppPersistentDataService} from "../infra/app-persistent-data.service";
import {AppLoggerService} from "../infra/app-logger.service";
import * as _ from "lodash";
import {utils} from '../../../utils/utils';
import {SystemHealthService} from "../infra/system-health.service";
import {TranslateService} from "ng2-translate";
import {Observable} from "rxjs";
import {AppConfigService} from "../infra/app-config.service";

export enum MONITOR_ERROR_TYPE {
  DEVICE_ERROR = <any>'DEVICE_ERROR',
  RECORDING_ERROR = <any>'RECORDING_ERROR',
  SYS_HEALTH_ERROR = <any>'SYS_HEALTH_ERROR'
}

export enum MONITORED_SOURCE_TYPE {
  ALL = <any>'ALL', //if user wants to remove all sources errors
  VIDEO = <any>'VIDEO',
  SCREEN = <any>'SCREEN',
  AUDIO = <any>'AUDIO',
  START_RECORDING = <any>'START_RECORDING',
  RESUME_RECORDING = <any>'RESUME_RECORDING',
  STOP_RECORDING = <any>'STOP_RECORDING',
  PAUSE_RECORDING = <any>'PAUSE_RECORDING',
  CANCEL_RECORDING = <any>'CANCEL_RECORDING',
  RECOVER_RECORDING = <any>'RECOVER_RECORDING',
  DISKSPACE = <any>'DISKSPACE'
}

@Injectable()
export class AppMonitoringAgentErrorDataService {

  private _ready: Promise<boolean>;

  /**
   * an object represents the application errors data for the monitoring agent
   *  {
   *      APP_STATUS: OK / ERROR,
   *      DEVICE_ERROR: {
   *       MONITORED_SOURCE_TYPE: {
   *        text: error string,
   *        time: time in millis
   *       }
   *       ....
   *      },
   *      RECORDING_ERROR: {
   *        MONITORED_SOURCE_TYPE:  {
   *        text: error string,
   *        time: time in millis
   *       }
   *       ...
   *      }
   *      SYS_HEALTH_ERROR: {
   *        MONITORED_SOURCE_TYPE:  {
   *        text: error string,
   *        time: time in millis
   *       }
   *       ...
   *      }
   *  }
   * @type {{}}
   */
  private monitoringAgentErrorData = {};

  constructor(private log: AppLoggerService,
              private translate: TranslateService,
              private appConfig: AppConfigService,
              private persistentData: AppPersistentDataService,
              private systemHealthService: SystemHealthService)
  {
    let monitorConfig = this.appConfig.getConfigData('config.shared.monitoring');
    if(monitorConfig && !_.isEmpty(monitorConfig) && monitorConfig['enabled']) {
      this.ready()
        .then(() => {
          Observable.timer(0, 5000).subscribe(() => {
            this.getMonitoringErrorData();
          })
        })
    }
  }

  private ready() : Promise<boolean>
  {
    if(!this._ready) {
      this._ready = this.persistentData.get('appMonitoringAgentErrorData')
        .then((data) => {
          if(data) {
            this.monitoringAgentErrorData = data;
          }
          this.log.info('AppMonitoringAgentDataService: Ready()');
          return Promise.resolve(true);
        })
        .catch((err) => {
          this.log.error('AppMonitoringAgentErrorDataService: Failed to load app monitoring agent error data.' + utils.stringifyMsg(err));
          this.monitoringAgentErrorData = {};
          return Promise.resolve(false);
        })
    }
    return this._ready;
  }

  public setError(errorType: MONITOR_ERROR_TYPE, source: MONITORED_SOURCE_TYPE, error: string, params = null)
  {
    Promise.all([this.ready(), this.getTranslationPromise(error, params)])
      .then(([ready, errorMsg]) => {
        if(_.isEmpty(this.monitoringAgentErrorData[errorType])) {
          this.monitoringAgentErrorData[errorType] = {};
        }

        this.monitoringAgentErrorData[errorType][source] = {
          'text': errorMsg,
          'time': new Date().getTime()
        };
        this.updateAppStatusData();
        this.updatePersistentData();
      });
  }

  public clearError(errorType: MONITOR_ERROR_TYPE, source: MONITORED_SOURCE_TYPE)
  {
    this.ready()
      .then(() => {
        if(!_.isEmpty(this.monitoringAgentErrorData[errorType])) {
          //remove all error type errors
          if(source === MONITORED_SOURCE_TYPE.ALL) {
            this.monitoringAgentErrorData[errorType] = {};
            //remove specific source error if exists
          } else if(this.monitoringAgentErrorData[errorType][source]) {
            delete this.monitoringAgentErrorData[errorType][source];
          }
        }
        this.updateAppStatusData();
        this.updatePersistentData();
      });
  }

  public getMonitoringErrorData()
  {
    return this.ready()
      .then(() => {
        return this.checkSysHealth();
      })
      .then(() => {
        return this.monitoringAgentErrorData;
      });
  }

  private checkSysHealth()
  {
    return this.systemHealthService.checkSystemHealth(false, true) //promise will never be rejected
      .then((sysHealth) => {
        if (_.isObject(sysHealth) && !_.isEmpty(sysHealth) &&
          sysHealth['status'] === this.systemHealthService.HEALTH_STATUS.ERROR) {
          let params = sysHealth['params'] ? sysHealth['params'] : null;
          this.setError(MONITOR_ERROR_TYPE.SYS_HEALTH_ERROR, MONITORED_SOURCE_TYPE.DISKSPACE, sysHealth['message'], params);
        } else {
          this.clearError(MONITOR_ERROR_TYPE.SYS_HEALTH_ERROR, MONITORED_SOURCE_TYPE.DISKSPACE);
        }
      })
      .catch((err) => {
        this.log.error('AppMonitoringAgentErrorDataService: Failed to get sysHealth. ' + utils.stringifyMsg(err));
      });
  }

  private updateAppStatusData()
  {
    let appStatus =
      _.isEmpty(this.monitoringAgentErrorData[MONITOR_ERROR_TYPE.DEVICE_ERROR]) &&
      _.isEmpty(this.monitoringAgentErrorData[MONITOR_ERROR_TYPE.RECORDING_ERROR]) &&
      _.isEmpty(this.monitoringAgentErrorData[MONITOR_ERROR_TYPE.SYS_HEALTH_ERROR]) ? 'OK' : 'ERROR';

    this.monitoringAgentErrorData['APP_STATUS'] = appStatus;
  }

  private updatePersistentData()
  {
    let monitorConfig = this.appConfig.getConfigData('config.shared.monitoring');
    if(monitorConfig && !_.isEmpty(monitorConfig) && monitorConfig['enabled']) {
      this.log.info('AppMonitoringAgentErrorDataService: update monitoring agent error data in app persistent data file');
      this.persistentData.set("appMonitoringAgentErrorData", this.monitoringAgentErrorData);
    }
  }

  private getTranslationPromise(msg, params = null)
  {
    return params ? this.translate.get(msg, params).toPromise() : this.translate.get(msg).toPromise();
  }

}



