import {Component, ViewEncapsulation, ViewChild, ElementRef, HostListener} from '@angular/core';
import {RecordingDetailsService} from "../../../../shared/services/recording/recording-details.service";
import * as _ from "lodash";
import {PanelsService} from "../../../../shared/services/infra/panels.service";
import {AppLoggerService} from "../../../../shared/services/infra/app-logger.service";
import {AppNotificationsService} from "../../../../shared/services/infra/app-notifications.service";
import {utils} from "../../../../utils/utils";
import {RecordingManagerService} from "../../services/recording-manager.service";
import {SchedulingService} from "../../services/scheduling.service";
import {TranslateService} from "ng2-translate";

@Component({
  selector: 'recording-finalize',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './recording-finalize.component.html',
  styleUrls:  ['./recording-finalize.component.scss', './../../styles/recording-details.scss'],
})
export class RecordingFinalizeComponent
{

  private mySubscription = null;
  tempRecordingDetails = {};
  private validSave = true;
  isSaveClicked = false;
  selectedUser = null;
  alertMsg = '';

  @ViewChild('firstInput')
  private firstInput: ElementRef;

  constructor(private recordingDetailsService: RecordingDetailsService, private recordingManagerService: RecordingManagerService,
              private panelsService: PanelsService, private log: AppLoggerService, private notificationService: AppNotificationsService,
              private schedulingService: SchedulingService, private translate: TranslateService)
  {
    this.mySubscription = this.panelsService.currentPanel$.subscribe(
      panel => {
        if(panel['type'] === 'recording-details-finalize' && panel['active'] === true) {
          this.tempRecordingDetails = _.cloneDeep(this.recordingDetailsService.getRecordingDetails());
          this.selectedUser = { id: this.tempRecordingDetails['userName'] };
        }
      });
  }

  ngOnInit()
  {
    this.validSave = true;
    if(this.shouldDisplayAnAlert()) {
      this.setRelevantAlert();
    }
    this.setFocusOnFirstInput();
  }

  /* Accessibility */
  private setFocusOnFirstInput() {
    this.firstInput.nativeElement.focus();
  }

  filterUserList(event)
  {
    this.recordingDetailsService.filterUserList(event.query);
  }

  getUserList()
  {
    return this.recordingDetailsService.userList;
  }

  onSaveDetailsClick()
  {
    this.isSaveClicked = true;
    if(this.recordingDetailsService.isUserNameValidForSave(this.selectedUser)) {
      this.log.info('Save current recording was clicked by the user.');
      this.tempRecordingDetails['userName'] = _.isObject(this.selectedUser) ? this.selectedUser['id'] : this.selectedUser;
      this.recordingDetailsService.setRecordingDetails(this.tempRecordingDetails);
      return this.recordingManagerService.finalizeCurrentRecording(true)
        .catch(() => {}) //there is nothing to do here, all is being taken case of in recording manager
        .then(() => {
          this.panelsService.updateCurrentPanel({type: 'recording-details-finalize', active: false});
        });
    } else {
      this.validSave = false;
    }
  }

  onCancelDetailsClick()
  {
    this.panelsService.updateCurrentPanel({type: 'recording-details-finalize', active: false});
  }

  private isUserNameValid()
  {
    return this.recordingDetailsService.isUserNameValid(this.selectedUser) && this.validSave;
  }

  private isUserListOffline()
  {
    return this.recordingDetailsService.isUserListOffline();
  }

  private setRelevantAlert()
  {
    let alertMsg = '';
    let params = {};
    //if it is a scheduled event alert
    if(!_.isEmpty(this.schedulingService.getCurrentEvent())) {
      let remainingTime = this.schedulingService.getCurrentEventRemainingInMin();
      if(remainingTime)
        alertMsg = remainingTime === 1 ?
          'SCHEDULING.save_before_end_notification_minute' : 'SCHEDULING.save_before_end_notification_minutes';
      params['value'] = remainingTime;
    }

    this.translate.get(alertMsg, params).subscribe((res: string) => {
      this.alertMsg = res;
    });
  }

  private onUserNameFieldClick()
  {
    this.validSave = true;
  }

  isReadOnly()
  {
    return this.recordingDetailsService.isReadOnly();
  }

  isUserNameDisabled()
  {
    return this.recordingDetailsService.isUserNameDisabled();
  }

  ngOnDestroy()
  {
    this.mySubscription.unsubscribe();
  }

  shouldDisplayAnAlert()
  {
    return !_.isEmpty(this.schedulingService.getCurrentEvent());
  }

  onUserChange(text){
    this.recordingDetailsService.onAutoCompleteUserChange(text);
  }
}



