import {NgModule} from "@angular/core";
import { BrowserModule } from '@angular/platform-browser';
import {InfraModule} from "../../services/infra/infra.module";
import {TranslateModule} from "ng2-translate";
import {ManagementConfirmDialogComponent} from "../managment-confirm-dialog/management-confirm-dialog.component";
import {ConfirmDialogModule} from "primeng/components/confirmdialog/confirmdialog";
import {SharedDirectives} from "../../directives/shared-directives.module";

@NgModule({
  imports: [
    BrowserModule,
    InfraModule,
    TranslateModule.forRoot(),
    ConfirmDialogModule,
    SharedDirectives
  ],
  declarations: [
    ManagementConfirmDialogComponent
  ],
  exports: [
    ManagementConfirmDialogComponent
  ]
})
export class ManagementConfirmDialogModule {}



