import {Injectable} from "@angular/core";
import {AppLoggerService} from "../infra/app-logger.service";
import {AppPersistentDataService} from "../infra/app-persistent-data.service";
import * as _ from "lodash";
import {utils} from "../../../utils/utils";
import {DeviceRecoveryService} from "../capture-device/device-recovery.service";

@Injectable()
export class RecordingRecoveryService {

  constructor(private log: AppLoggerService,
              private persistencyService: AppPersistentDataService,
              private deviceRecoveryService: DeviceRecoveryService)
  {
  }

  /**
   * return a persistent recording data if there is one. null if not.
   * checking for existing recorded files
   */
  shouldRecoverRecordingSession()
  {
    return this.persistencyService.get('recording')
      .then((recData) => {
        if(!_.isEmpty(recData) && !_.isEmpty(recData['details']) && !_.isEmpty(recData['details']['files'])) {
          return recData;
        } else {
          return null;
        }
      })
      .catch((err) => {
        this.log.error('Failed to read recovery persistent data. ' + utils.stringifyMsg(err));
        return Promise.reject(err);
      });
  }

  canRecoverRecordingSession(): any
  {
    this.log.info('canRecoverRecordingSession()');
    let canRecover = true;
    return this.persistencyService.get('recording.details')
      .then((details) => {
        _.forEach(this.deviceRecoveryService.recoverySessions, function (sessionObj) {
          canRecover = canRecover && (_.isEmpty(details['files'][sessionObj['name']]) || sessionObj['recoveryReady']);
        });
        return canRecover;
      })
      .catch((err) => {
        return Promise.reject(err);
      });
  }


  /**
   * at the moment just checks for disconnection or not
   * @return {string}
   */
  getRecoveryFailureReason(): string
  {
    if(_.isEmpty(this.deviceRecoveryService.recoverySessions)) {
      return this.deviceRecoveryService.DEVICE_STATE.NONE;
    } else {
      let failureReason = this.deviceRecoveryService.DEVICE_STATE.FAILED;
      _.forEach(this.deviceRecoveryService.recoverySessions, (obj) => {
        if(obj && !_.isEmpty(obj['reason']) &&
          (obj['reason'] === this.deviceRecoveryService.DEVICE_STATE.DISCONNECTED || obj['reason'] === 'AUDIO-' + this.deviceRecoveryService.DEVICE_STATE.DISCONNECTED ||
          obj['reason'] === 'NO_VIDEO_DEVICES' || obj['reason'] === 'NO_SCREEN_DEVICES' || obj['reason'] === 'NO_AUDIO_DEVICES')) {
          failureReason = this.deviceRecoveryService.DEVICE_STATE.DISCONNECTED;
        }
      });
      return failureReason;
    }
  }

  resetRecoverySessions()
  {
    this.deviceRecoveryService.resetRecoverySessions();
  }

  getRecoverySessionsReadyPromise()
  {
    return this.deviceRecoveryService.recoverySessionsReadyPromise;
  }

  getRecoveryDisconnectionState()
  {
    return this.deviceRecoveryService.DEVICE_STATE.DISCONNECTED;
  }

}



