import {NgModule} from "@angular/core";
import { BrowserModule } from '@angular/platform-browser';
import {LogCollectorService} from "./log-collector.service";
import {InfraModule} from "../../services/infra/infra.module";
import {LogCollectorComponent} from "./log-collector.component";
import {TranslateModule} from "ng2-translate";
import {ProgressBarModule} from "primeng/components/progressbar/progressbar";
import {ConfirmDialogModule} from "primeng/components/confirmdialog/confirmdialog";
import {ManagementConfirmDialogModule} from "../managment-confirm-dialog/menagement-confirm-dialog.module";
import {SharedDirectives} from "../../directives/shared-directives.module";

@NgModule({
  imports: [
    BrowserModule,
    InfraModule,
    TranslateModule.forRoot(),
    ProgressBarModule,
    ConfirmDialogModule,
    ManagementConfirmDialogModule,
    SharedDirectives
  ],
  declarations: [
    LogCollectorComponent
  ],
  providers: [
    LogCollectorService
  ],
  exports: [
    LogCollectorComponent
  ]
})
export class LogCollectorModule {}



