import { Component } from '@angular/core';
import {AppConfigService} from "../../../shared/services/infra/app-config.service";
import {ipcWrapper} from "../../../utils/ipcWrapper";

@Component({
    selector: 'app-info',
    templateUrl: './app-info.component.html',
    styleUrls:  ['./app-info.component.scss'],
})
export class AppInfoComponent {
    _config : any[];
    constructor(private appConfig:AppConfigService) {
        this._config = this.appConfig.getConfigData("config.shared");
    }

    openOpenSourceLicenses() {
      ipcWrapper.sendIPC("open-external-link","https://corp.kaltura.com/content/kaltura-lecture-capture-application-open-source-libraries");
    }
}



