import {Injectable} from "@angular/core";
import {TranslateService} from 'ng2-translate';
import {AppLoggerService} from "../shared/services/infra/app-logger.service";
import {ipcWrapper} from "../utils/ipcWrapper";
import {utils, promiseState} from "../utils/utils";
import * as _ from "lodash";
import {MinimizeService} from "../utils/minimize.service";
import {AuthenticationService} from "../shared/services/infra/authentication.service";
import {AppConfigService} from "../shared/services/infra/app-config.service";
import {DefaultSettingsService} from "../shared/services/capture-device/default-settings.service";
import {AppStateService} from "../shared/services/infra/app-state.service";
import {VideoDeviceService} from "../shared/services/capture-device/video-device.service";
import {AudioDeviceService} from "../shared/services/capture-device/audio-device.service";
import {SessionStateService} from "../shared/services/infra/session-state.service";
import {InputsManagementService} from "./classroom-capture/components/inputs-management/inputs-management.service";

@Injectable()
export class AppService {

  private isDefaultSettingsEnabled = false;

  constructor(
    private log: AppLoggerService,
    private appState: AppStateService,
    private appConfig: AppConfigService,
    private translate: TranslateService,
    private minimizeService: MinimizeService,
    private videoService: VideoDeviceService,
    private audioService: AudioDeviceService,
    private authService: AuthenticationService,
    private defaultSettings: DefaultSettingsService,
    private sessionStateService: SessionStateService,
    private inputsManagementService: InputsManagementService)
  {
    this.registerToInitSessionsEvent();
  }

  initLocale()
  {
    // this language will be used as a fallback when a translation isn't found in the current language
    this.translate.setDefaultLang('en');
    //get os locale and load translation
    ipcWrapper.sendIPC('getOsLocale')
      .then((locale: string) => {
        this.translate.use(this.manipulateLocaleCode(locale));
      })
      .catch((err) => {
        this.log.error(utils.stringifyMsg(err));
        this.translate.use('en');
      });
  }

  private registerToInitSessionsEvent()
  {
    this.appState.notifyDeviceComponentInit$.subscribe(() => {
      this.log.info('Application sessions are being re-init after recording ended');
      this.initCaptureSessions()
        .catch((err) => {
          this.log.error('AppService: Failed to init capture sessions after' +
            ' notifyDeviceComponentInit. ' + utils.stringifyMsg(err) );
        });
    });
  }


  registerForWindowNotifications()
  {
    ipcWrapper.registerForNotification('on-window-maximize', () => {
      this.minimizeService.cancelTrayMinimizationSubscription();
    });
    ipcWrapper.registerForNotification('on-window-move', () => {
      this.minimizeService.cancelTrayMinimizationSubscription();
    });
  }

  onDocumentClickHandler()
  {
    this.minimizeService.cancelTrayMinimizationSubscription();
    this.authService.restartLoginIdleInterval();
    if(this.isDefaultSettingsEnabled) this.defaultSettings.restartInterval();
  }

  handleDefaultSettingsInit()
  {
    if(this.appConfig.getConfigData('config.captureApp.restoreDefaultSettings.enable')) {
      this.isDefaultSettingsEnabled = true;
      this.defaultSettings.start(() => {
        this.inputsManagementService.fireRefreshInputsEvent();
      });
    }
  }

  initCaptureSessions()
  {
    this.log.info('AppService: initCaptureSessions');
    let captureSessionsToInitPromises = [];
    return this.appState.getVisualSessionsNamesAndDefaultTypesForInit()
      .then(sessions => {
        _.forEach(sessions, (defaultType: string, sessionName) => {
          captureSessionsToInitPromises.push(this.initVideoCaptureDevice(sessionName, defaultType));
        });
        //adding audio session
        captureSessionsToInitPromises.push(this.initAudioCaptureDevice());
        //init all
        return utils.promiseAllSettled(captureSessionsToInitPromises);
      })
      .then((results: promiseState[]) => {
        //no need to do anything, notifications for the user will be sent during device load
        //and app will handle device failures. Just logging errors...
        const failedPromises = results.filter(ps => ps.status !== 'fulfilled');
        const errMsgsReducer = (concatErrMsgs, failedPromise) => concatErrMsgs += (utils.stringifyMsg(failedPromise.reason) + '\n');
        if(!_.isEmpty(failedPromises))
          this.log.error('AppService: Error while init capture sessions ' + failedPromises.reduce(errMsgsReducer, ''));

        this.log.info('appService sending sessionsInit event');
        this.sessionStateService.sessionsInit.next();
      });
  }

  loadApp()
  {
    this.log.info('Loading Kaltura Classroom app.');
    return this.appState.sessionsAreReadyPromise
      .then(() => {
        this.log.info('classroom-app-load done loading');
        return ipcWrapper.sendIPC("capture-app-load");
      });
  }

  private initVideoCaptureDevice(sessionName: string, defaultCaptureType: string)
  {
    return this.videoService.initDevice(sessionName, defaultCaptureType)
      .then(() => {
        return this.videoService.startDevice(sessionName, {left: 0, top: 0, width: 0, height: 0}); //we don't want to preview the video
      })
      .catch((err) => {
        this.log.error(sessionName + ' init Error ' + ' - ' + utils.stringifyMsg(err));
        this.videoService.handleDeviceInitError(sessionName, err, {left: 0, top: 0, width: 0, height: 0}); //we don't want to preview the video
      });
  }

  private initAudioCaptureDevice()
  {
    return this.audioService.initAudioDevice();
  }

  /**
   * according to product - all en-XXXX should be mapped to en locale
   * @param locale
   * @returns {any}
   */
  private manipulateLocaleCode(locale)
  {
    if (!locale){
      return 'en';
    }
    return locale.substr(0, 2);
  }
}



