import {Injectable} from "@angular/core";
import {AppLoggerService} from "../../../shared/services/infra/app-logger.service";
import {utils} from "../../../utils/utils";
import * as _ from "lodash";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {ResourceIdService} from "../../../shared/services/resource-id/resource-id.service";
import {AppPersistentDataService} from "../../../shared/services/infra/app-persistent-data.service";
import {AppConfigService} from "../../../shared/services/infra/app-config.service";
import {LogCollectorService} from "../../../shared/components/log-collector/log-collector.service";
import {AppStateService} from "../../../shared/services/infra/app-state.service";

@Injectable()
export class SettingsService {

  private resourceName: string;
  private maxNumOfInputs: number;
  private lastResourceNameError = "";

  constructor( private log: AppLoggerService,
               private appState: AppStateService,
               private appConfig: AppConfigService,
               private resourceIdService: ResourceIdService,
               private logCollectorService: LogCollectorService,
               private appPersistency: AppPersistentDataService
  ) {}

  public onSave()
  {
    this.appConfig.setConfigData('config.captureApp.maxNumberOfInputs', this.maxNumOfInputs);
    return this.renameResourceName(this.resourceName)
      .then(() => {
        this.lastResourceNameError = "";
      })
      .catch(err => {
        this.lastResourceNameError = utils.getAPIErrorMessage(err)
      });
  }

  public onCancel()
  {
    this.getMaxNumOfInputs(); //reset number of max inputs value
  }

  public getMaxNumOfInputs()
  {
    this.maxNumOfInputs = this.appConfig.getConfigData('config.captureApp.maxNumberOfInputs');
    return this.maxNumOfInputs;
  }

  public setMaxNumOfInputs(numOfInputs)
  {
    this.maxNumOfInputs = numOfInputs;
  }

  public isInProgress()
  {
    return this.isUploadingLogs();
  }

  public hasAdminRights()
  {
    return ipcWrapper.sendIPC('hasAdminRights');
  }

  public getUserPrivacyStatus()
  {
    return ipcWrapper.sendIPC('getPartnerUserPrivacyStatus');
  }

  public getResourceName()
  {
    this.resourceName = this.resourceIdService.resourceName;
    return this.resourceName;
  }

  public getResourceId()
  {
    return this.resourceIdService.resourceId;
  }

  public refreshResourceName()
  {
    return this.resourceIdService.refreshResourceNameFromServer();
  }

  public renameResourceName(resourceName)
  {
    return this.resourceIdService.renameResourceName(resourceName);
  }

  public saveCurrentSettingsAsDefault()
  {
    return this.appPersistency.get('')
      .then(persistentData => {
        let defaultSettings = {};
        let sessionNames = _.concat(this.appState.getSessionsNames(true), this.appState.getSessionsNames(false));
        _.forEach(sessionNames, (sessionName) => {
          defaultSettings[sessionName] = persistentData[sessionName] ? persistentData[sessionName] : {preview: 'disabled'};
        });
        return this.appPersistency.set('defaultSettings', defaultSettings);
      })
      .catch((e) => {
        this.log.error('Failed to save default settings. ' + utils.stringifyMsg(e));
      })
  }

  public openLogsDir()
  {
    return ipcWrapper.sendIPC('openDirectory', this.appConfig.getConfigData('config.shared.logsDir'))
      .then(success => {
        if(!success) this.log.error('Failed to open logs folder');
      });
  }

  public askBeforeLeave()
  {
    return this.logCollectorService.askBeforeLeave();
  }

  public isUploadingLogs()
  {
    return this.logCollectorService.isUploadingLogs();
  }

  public setResourceNameLocally(resourceName)
  {
    this.resourceName = resourceName;
  }

  public getLastResourceNameError()
  {
    return this.lastResourceNameError;
  }

  public setLastResourceNameError(lastResourceNameError)
  {
    this.lastResourceNameError = lastResourceNameError;
  }

}



