import {Injectable} from "@angular/core";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {utils} from "../../../utils/utils";

@Injectable()
export class AppLoggerService {

  constructor(){}

  debug(msg)
  {
    return ipcWrapper.sendIPC('NativeLogger_Debug', utils.stringifyMsg(msg));
  }

  info(msg)
  {
    console.log(utils.stringifyMsg(msg));
    return ipcWrapper.sendIPC('NativeLogger_Info', utils.stringifyMsg(msg));
  }

  warn(msg)
  {
    console.log(utils.stringifyMsg(msg));
    return ipcWrapper.sendIPC('NativeLogger_Warn', utils.stringifyMsg(msg));
  }

  error(msg)
  {
    console.log(utils.stringifyMsg(msg));
    return ipcWrapper.sendIPC('NativeLogger_Error', utils.stringifyMsg(msg));
  }

}



