import {Component, Input} from '@angular/core';
import {TranslateService} from "ng2-translate";
import {LogCollectorService} from "./log-collector.service";
import {AppLoggerService} from "../../services/infra/app-logger.service";

@Component({
  selector: 'log-collector',
  templateUrl: './log-collector.component.html',
  styleUrls:  ['./log-collector.component.scss']
})
export class LogCollectorComponent {

  @Input()
  componentSize: string;

  constructor(private logger: AppLoggerService,
              private translate: TranslateService,
              private logCollectorService: LogCollectorService)
  {}

  onLogClick()
  {
    this.logCollectorService.resetFakeLogUploadProgress();
    this.logCollectorService.onLogClick();
  }

  getUploadProgress()
  {
    return this.logCollectorService.getFakeLogUploadProgress();
  }

  isUploadingLogs()
  {
    return this.logCollectorService.isUploadingLogs();
  }

  onOpenLogsDirClick()
  {
    this.logCollectorService.openLogsDir();
  }

}



