import { Injectable } from '@angular/core';
import {ipcWrapper} from "../../../utils/ipcWrapper";

@Injectable()
export class AppPersistentDataService
{
  constructor() {
  }

  public get(path)
  {
    return ipcWrapper.sendIPC('getPersistency', path);
  }

  public set(path, data)
  {
    return ipcWrapper.sendIPC('setPersistency', path, data);
  }

}



