import {Component, NgZone} from '@angular/core';
import {AppStateService} from "../../../../../shared/services/infra/app-state.service";
import {AppLoggerService} from "../../../../../shared/services/infra/app-logger.service";
import {DevicePreviewService} from "../../../../../shared/services/capture-device/device-preview.service";
import {AudioDeviceService} from "../../../../../shared/services/capture-device/audio-device.service";
import {AppConfigService} from "../../../../../shared/services/infra/app-config.service";
import {PanelsService} from "../../../../../shared/services/infra/panels.service";
import {utils} from "../../../../../utils/utils";

@Component({
  selector: 'audio-input',
  templateUrl: './audio-input.component.html',
  styleUrls:  ['./audio-input.component.scss', './../../../styles/device-container.scss'],
  providers: []
})
export class AudioInputComponent {

  readonly SETTINGS_PANEL = 'audio-device-settings';

  private deviceNotificationSub = null;
  private audioLevelSub = null;

  audioLevel0 = 0;
  audioLevel1 = 0;

  constructor(
    private zone: NgZone,
    private log: AppLoggerService,
    private appState: AppStateService,
    private appConfig: AppConfigService,
    private panelsService: PanelsService,
    private audioDeviceService: AudioDeviceService,
    private devicePreviewService : DevicePreviewService)
  {
  }

  ngOnInit(): void
  {
    this.addListeners();
  }

  ngOnDestroy(): void
  {
    this.removeListeners();
  }

  isAppRecording()
  {
    return this.audioDeviceService.isAppRecording();
  }

  onAudioPreviewClick()
  {
    this.getAudioPreviewAction(!this.isAudioEnabled())
      .catch((err) => {
        this.log.error('Audio-Input: failed to change audio device preview state to ' +
          !this.isAudioEnabled() + ' :' + utils.stringifyMsg(err))
      });
  }

  onAudioSettingsClick()
  {
    this.panelsService.updateCurrentPanel({
      type : this.SETTINGS_PANEL,
      active : !this.panelsService.isPanelActive(this.SETTINGS_PANEL)});
  }

  isAudioSettingsPanelActive()
  {
    return this.panelsService.isPanelActive(this.SETTINGS_PANEL);
  }

  disabledAudioSettingsButton() : boolean
  {
    return  this.audioDeviceService.isAudioSettingsDisabled() ||
      (this.panelsService.isCurrentPanelActive() && //there is an active panel
      !this.panelsService.isPanelActive(this.SETTINGS_PANEL)); //the active panel is not audio settings
  }

  disableAudioPreviewButton()
  {
    return this.audioDeviceService.shouldDisableAudioButton() || this.panelsService.isCurrentPanelActive();
  }

  isAudioEnabled()
  {
    return this.audioDeviceService.isAudioEnabled(
      this.appConfig.getConfigData('config.captureApp.audioOnlyRecording'));
  }

  devicePreviewStyleClass()
  {
    return this.isAudioEnabled() ?
      'audio-enabled-button device-preview-in-progress' :
      'audio-disabled-button device-preview-stopped';
  }

  devicePreviewAriaText()
  {
    return this.isAudioEnabled ?
      'CAPTURE_DEVICE.disableAudio' :
      'CAPTURE_DEVICE.enableAudio';
  }

  private getAudioPreviewAction(enable)
  {
    return enable ?
      this.audioDeviceService.enableAudioDevice() :
      this.audioDeviceService.disableAudioDevice();
  }

  private addListeners()
  {
    this.audioLevelSub =
      this.devicePreviewService.audioLeverSubject$.subscribe(
      audioLevel => {
        this.zone.run(() => this.audioLevel0 = audioLevel['Channel0']);
        this.zone.run(() => this.audioLevel1 = audioLevel['Channel1']);
      }
    );
  }

  private removeListeners()
  {
    if(this.deviceNotificationSub) this.deviceNotificationSub.unsubscribe();
    if(this.audioLevelSub) this.audioLevelSub.unsubscribe();
  }


}



