import {Component, HostListener, OnInit} from '@angular/core';
import {TranslateService} from 'ng2-translate';
import {ipcWrapper} from "../utils/ipcWrapper";
import {AppStateService} from "../shared/services/infra/app-state.service";
import {AppNotificationsService} from "../shared/services/infra/app-notifications.service";
import {AppLoggerService} from "../shared/services/infra/app-logger.service";
import {utils} from "../utils/utils";
import {SystemHealthService} from "../shared/services/infra/system-health.service";
import {AppMonitoringAgentService} from "../shared/services/monitoring/app-monitoring-agent.service";
import {SchedulingService} from "./classroom-capture/services/scheduling.service";
import {SystemDataService} from "../shared/services/infra/system-data.service";
import {MinimizeService} from "../utils/minimize.service";
import {AppContainerProperties} from "../shared/components/app-container/app-container.component"
import {AccessibilityService} from "../shared/services/infra/accessability.service";
import {DefaultSettingsService} from "../shared/services/capture-device/default-settings.service";
import {AppConfigService} from "../shared/services/infra/app-config.service";
import {AuthenticationService} from "../shared/services/infra/authentication.service";
import {AppService} from "./app.service";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  providers: [AppService]
})
export class AppComponent {

  private containerProperties: AppContainerProperties;

  constructor(
              private log: AppLoggerService,
              private appService: AppService,
              private appNotificationsService: AppNotificationsService,
              private schedulingService: SchedulingService,
              private systemData: SystemDataService,
              private sysNotificationService: SystemHealthService,
              private appMonitoring: AppMonitoringAgentService,
              private accessibilityService: AccessibilityService)
  {
    this.log.info("in AppComponent::constructor");
    this.appService.initLocale();
    this.sysNotificationService.start();
    this.appMonitoring.start();
    this.appService.registerForWindowNotifications();
    this.containerProperties = this.getContainerProperties();
    this.loadApplication();
  }

  private loadApplication()
  {
    this.appService.initCaptureSessions()
      .then(() => {
        this.appService.loadApp(); //need to wait for window to be ready after splash screen
      })
      .then(() => {
        this.appService.handleDefaultSettingsInit();
      });
  }

  /**
   * The Main reason why doing it in a method way is to add outline focussquare
   * on tab focus ONLY and not on click focus
   * @param KeyboardEvent
   */
  @HostListener('document:keyup', ['$event'])
  onKeyUp(event:KeyboardEvent) {
    return this.accessibilityService.markNavigation(event);
  }

  @HostListener('document:click', ['$event'])
  onDocumentClick(){
    this.appService.onDocumentClickHandler();
  }

  getContainerProperties() : AppContainerProperties{
    let properties: AppContainerProperties = {
      header: {
        logoIcon: 'logo-icon',
        buttons: ['minimize_to_tray', 'maximize', 'minimize', 'debug']
      },
      navbar: {
        links: [{
            linkClass: 'home-nav',
            label: 'MENU.home',
            routerLink: '/'
          },
          {
            linkContainer: 'calendar-item',
            linkClass: 'calendar-nav',
            label: 'SCHEDULING.scheduling',
            routerLink: '/scheduling',
            shouldRender: () => { return this.schedulingService.isSchedulingEnabled; }
          },
          {
            linkClass: 'info-nav',
            label: 'APP_INFO.title',
            routerLink: '/info',
            shouldDisableWhenNotStable: true
          },
          {
            linkContainer: '',
            linkClass: 'settings-nav',
            label: 'SETTINGS.panel_title',
            routerLink: '/settings/general-settings',
            shouldDisableWhenNotStable: true
          }
        ]
      },
      showBackLight: true
    };
    return properties;
  }
}



