import { Injectable } from '@angular/core';
import {Http, Headers, Response} from '@angular/http';
import {AppConfigService} from "./app-config.service";
import * as _ from 'lodash';
import {utils} from '../../../utils/utils';
import 'rxjs/Rx';
import {Observable} from 'rxjs/Observable';
import {AppLoggerService} from "./app-logger.service";
import {AppPersistentDataService} from "./app-persistent-data.service";

@Injectable()
export class KalturaApiService {
  _id: number = 0;
  _ks: string ;
  _config: any;
  _ks_expiry: Date;

  constructor(private http: Http,
              private logger: AppLoggerService,
              private appConfig:AppConfigService,
              private appPersistency: AppPersistentDataService) {
    /*
     this.listUsers("web").subscribe( res => {
     console.warn("listUsers",res);
     });
     */
  }

  public listUsers(startsWith:string) : Observable<any>
  {
    return this.apiRequest(
      [{ service: 'user',
        action: 'list',
        "filter:objectType": "KalturaUserFilter",
        "filter:idOrScreenNameStartsWith": startsWith,
        'pager:pageSize': 10},
        { service: 'user',
          action: 'list',
          "filter:objectType": "KalturaUserFilter",
          "filter:firstNameOrLastNameStartsWith": startsWith,
          'pager:pageSize': 10}])
      .map( res => {
        let userList = [];
        _.each(res, (result, key) => {
          userList = _.unionBy(userList, result['objects'], 'id');
        });
        return userList;
      });
  }

  /**
   *
   * @param {string} resourceId
   * @param {string} eventType
   * @param {any} data
   * @param {boolean} shouldLog
   * @param {boolean} rawData raw data or private data (image or text)
   * @returns {Observable<any>}
   */
  public reportMonitoringData(resourceId: string, eventType: string, data: any, shouldLog: boolean, rawData: boolean) : Observable<any>
  {
    let reqObject = {
      "service": 'beacon_beacon',
      "action": 'add',
      "shouldLog" : shouldLog ? 1 : 0,
      "beacon:objectType": 'KalturaBeacon',
      "beacon:relatedObjectType": "1",
      "beacon:eventType": eventType,
      "beacon:objectId": resourceId
    };
    if(rawData) {
      reqObject['beacon:rawData'] = _.isObject(data) ? JSON.stringify(data) : data;
    } else {
      reqObject['beacon:privateData'] = _.isObject(data) ? JSON.stringify(data) : data;
    }
    return this.apiRequest(reqObject);
  }

  /**
   *
   * @param startDate time in millis
   * @param endDate time in millis
   * @return {Observable<R>}
   */
  public getScheduledEvents(startDate: number, endDate: number, resourceId:string) : Observable<any>
  {
    this.logger.debug("Sending API get request scheduled event: " + resourceId);
    return this.apiRequest(
      { service: 'schedule_scheduleevent',
        action: 'list',
        "filter:objectType": "KalturaScheduleEventFilter",
        "filter:endDateGreaterThanOrEqual": startDate / 1000, //ms to sec (server units)
        "filter:startDateLessThanOrEqual": endDate / 1000, // ms to sec (server units)
        "filter:orderBy": "+startDate,+createdAt",
        "filter:resourceIdsLike": resourceId,
        "filter:recurrenceTypeIn": "0,2",
        "filter:statusEqual": 2,//only Active events
        'pager:pageSize': 1000})
      .map( res => {
        return res.objects;
      });
  }

  public getEntry(entryId: string) : Observable<any>
  {
    this.logger.debug("Sending API get request for entry id: " + entryId);
    return this.apiRequest(
      { service: 'baseentry',
        action: 'get',
        entryId: entryId
      }
    );
  }

  public updateRedirectEntryId(entryId: string, redirectEntryId: string)  : Observable<any> {
    this.logger.debug("Sending API update request for entry id: " + entryId + " with redirectEntryId of: [" + redirectEntryId + "]");
    return this.apiRequest(
      { service: 'baseentry',
        action: 'update',
        entryId: entryId,
        "baseEntry:objectType" : "KalturaBaseEntry",
        "baseEntry:redirectEntryId" : redirectEntryId
      }
    );
  }

  public updateAdminTags(entryId: string, adminTags: string)  : Observable<any> {
    this.logger.debug("Sending API update request for entry id: " + entryId + " with AdminTags of: " + adminTags);
    return this.apiRequest(
      { service: 'baseentry',
        action: 'update',
        entryId: entryId,
        "baseEntry:objectType" : "KalturaBaseEntry",
        "baseEntry:adminTags" : adminTags
      }
    );
  }

  public getFlavors(flavorIds: string) : Observable<any>
  {
    this.logger.debug("Sending API list request for flavors ids: " + flavorIds);
    return this.apiRequest(
      { service: 'flavorparams',
        action: 'list',
        "filter:objectType": "KalturaScheduleEventFilter",
        "filter:idIn": flavorIds
      }
    ).map( res => {
      return res.objects;
    });
  }

  public apiRequest(params:any) : Observable<any> {
    let now=new Date();

    if (this._ks_expiry && now>this._ks_expiry) {
      this._ks=null;
    }

    if (this._ks) {
      return this._apiRequest(params);
    }

    return this.login().flatMap( res=> {
      return this._apiRequest(params);
    });
  }

  /**
   * sets user suggested tags in persistent data file [last 10 tags]
   * *** in future we might consider to use Kaltura api to fetch all relevant tags and remove this method ***
   * @param tags
     */
  public setSuggestedTags(tags: string[])
  {
    return this.getSuggestedTags('').toPromise()
      .then((oldTags) => {
        let allTags = _.uniq(oldTags.concat(tags));
        this.appPersistency.set('user.tags', _.takeRight(allTags, 10).join(','));
      });
  }

  /**
   * gets user suggested tags [last 10 used tags] from app persistent data
   * *** in future we might consider to use Kaltura api to fetch all relevant tags ***
   * @param containedString
   * @returns {Array}
     */
  public getSuggestedTags(containedString: string)
  {
    return Observable.fromPromise(this.appPersistency.get('user')
      .then((userData) => {
        let tags = [];
        if(userData && !_.isEmpty(userData) && userData['tags']) {
          let suggested = userData['tags'].split(',');
          _.forEach(suggested, (suggestedTag) => {
            if(suggestedTag.includes(containedString)) tags.push(suggestedTag);
          })
        }
        return tags;
      }));
  }

  private login (): Observable<any> {
    this._config = this.appConfig.getConfigData("config.shared");

    return this._apiRequest({
      service: "session",
      action: "startWidgetSession",
      "widgetId": '_' + this._config["partnerId"]
    }).flatMap((result) => {
      return this._apiRequest({
        service: "apptoken",
        action: "startSession",
        ks: result.ks,
        tokenHash: utils.sha1(result.ks + this._config["appToken"]),
        id: this._config["appTokenId"]
      }).map((result) => {
        this._ks = result.ks;
        let now = new Date();
        this._ks_expiry = new Date(now.getTime() + 1 * 60 * 60 * 1000);//1 hour
        return result;
      });
    });
  }


  private _apiRequest(requests:any): Observable<any> {

    let body:any = {
      ks: this._ks,
      format: 1,
      clientTag: "CRA:"+ this._config.app_version
    };

    if (_.isArray(requests)) {
      body['service'] = "multirequest";
      requests.forEach( (item,index) => {
        _.each(item, (value,key)=> {
          body[index + ":" + key] = value;
        });
      });
    } else {
      body=_.assign(body,requests);
    }
    let requestJSON =  JSON.stringify(body);

    this._id++;
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    this.logger.debug('API request [#'+this._id+'] for service '+ body['service'] + '::' + body['action']);
    return this.http.post(this._config['serviceUrl']+ `/api_v3/index.php`,requestJSON, {
      headers: headers
    }).map( (response:Response) => {
      let result=response.json();
      let headers=response.headers;
      this.logger.debug('API response for [#' + this._id + '] X-Me: [' + headers.get('X-Me') + '] X-Kaltura-Session: ' + headers.get('X-Kaltura-Session'));
      if (result && result.objectType === "KalturaAPIException") {
        throw Observable.throw ( result );
      }
      return result;
    }).catch( (error: any)=> {
        this.logger.warn('API response failed for #'+this._id + ' result.' +
          ' Token Id: ' + this._config["appTokenId"] +
          ' KS: ' + this._ks +
          ' KS expiry: ' + this._ks_expiry +
          ' Error msg: ' + utils.stringifyMsg(error));
        this._ks = null; //making sure we won't use the same KS in the next call
        return Observable.throw(error);
    });
  }

  public checkIsLive(entryId: string) : Observable<any>
  {
    let requestJSON = JSON.stringify({ service: 'livestream',
      action: 'isLive',
      "id": entryId,
      "protocol": "applehttp",
      "format": 1,
    });

    return this.http.post(this._config['serviceUrl']+ `/api_v3/index.php`,requestJSON, {
      headers: new Headers({'Content-Type': 'application/json'})
    }).map( (response:Response) => {
      let result = response.json();
      if (result && result.objectType === "KalturaAPIException") {
        throw Observable.throw(result);
      }
      return result;
    });
  }

}



