import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';
import Duration = moment.Duration;
import * as _ from 'lodash';

@Pipe({
  name: 'duration'
})
export class DurationPipe implements PipeTransform{

  transform(durationNumber: number) : string {
    if (_.isFinite(durationNumber)) {
      let duration = moment.duration(durationNumber);
      return this.padTo2Digits(duration.hours()) + ':' +  this.padTo2Digits(duration.minutes()) + ':' + this.padTo2Digits(duration.seconds());
    }
    else {
     return '';
    }
  }

  private padTo2Digits(number: number) {
    return (number < 10 ? '0' : '') + number;
  }
}



