import { Component, OnInit } from "@angular/core";
import { SchedulingService } from "../../services/scheduling.service";
import * as moment from 'moment';
import Duration = moment.Duration;
import {Observable, Subscription} from "rxjs";
import {ipcWrapper} from "../../../../utils/ipcWrapper";
import {AppStateService} from "../../../../shared/services/infra/app-state.service";
import {TranslateService} from "ng2-translate";


@Component({
  selector: 'recording-notifications',
  templateUrl: './recording-notifications.component.html',
  styleUrls:  ['./recording-notifications.component.scss']
})
export class RecordingNotificationsComponent implements OnInit{

  private countdownSubscription$: Subscription;
  private countdownTimeDuration: number;
  private isNextEventCountdownVisible: boolean = false;
  private _currentIcon: string = null;

  // const
  private static readonly COUNT_DOWN_DISAPPEAR_BEFORE_EVENT_START_SEC = 5;

  private readonly ICON_NAMES =
    {
      RECORDING: 'recording',
      SCHEDULED_EVENT: 'scheduled-event',
      DEFAULT: 'default'
    };

  constructor(private schedulingService: SchedulingService, private appState: AppStateService, private translate: TranslateService) {}

  ngOnInit(){
    this.countdownSubscription$ = Observable.timer(0, 1000)
      .subscribe(()=>{
        this.isNextEventCountdownVisible = this.schedulingService.shouldDisplayNextEventNotifications(RecordingNotificationsComponent.COUNT_DOWN_DISAPPEAR_BEFORE_EVENT_START_SEC);
        this.showCountDownAndNotification();
        this.changeCurrentIcon();
      });
  }

  get nextEvent(){
    return this.schedulingService.getNextEvent();
  }

  private showCountDownAndNotification(){
    if (this.isNextEventCountdownVisible) {
      let now = moment().valueOf();
      this.countdownTimeDuration = this.schedulingService.getNextEventRealStartTime()*1000 - now;
    }
    else {
      this.countdownTimeDuration = null;
    }
  }

  private changeCurrentIcon(){
    if (this.appState.isAppRecording() && this.appState.recordingPhase === 'recording'){
      this.currentIcon = this.ICON_NAMES.RECORDING;
    }
    else if (this.isNextEventCountdownVisible){
      this.currentIcon = this.ICON_NAMES.SCHEDULED_EVENT;
    }
    else {
      this.currentIcon = this.ICON_NAMES.DEFAULT;
    }
  }

  get currentIcon(): string {
    return this._currentIcon;
  }

  set currentIcon(newCurrentIconValue: string) {
    if (this._currentIcon !== newCurrentIconValue){
      this._currentIcon = newCurrentIconValue;
      this.translate.get("NOTIFICATIONS." + this._currentIcon).subscribe((localeText: string) => {
        ipcWrapper.sendSystemCommand('changeTaskBarIcon', this._currentIcon, localeText);
      });
    }
  }

  isNextEventLive() {
    let event = this.schedulingService.getNextEvent();
    return event && event.primaryRtmp;
  }
}



