import { CanDeactivate } from '@angular/router';
import { Injectable } from '@angular/core';
import { GeneralSettingsComponent } from "../components/general-settings/general-settings.component";

@Injectable()
export class GeneralSettingsComponentGuard implements CanDeactivate<GeneralSettingsComponent> {

  canDeactivate(generalSettingsComponent: GeneralSettingsComponent) {
    if (generalSettingsComponent) {
      return generalSettingsComponent.askBeforeLeave();
    }
    else {
      return Promise.resolve(true);
    }
  }
}



