import {NgModule} from '@angular/core';
import {InfraModule} from "../infra/infra.module";
import {RecordingDetailsService} from "./recording-details.service";
import {RecordingRecoveryService} from "./recording-recovery.service";
import {SlideService} from "./slides.service";
import {MonitoringModule} from "../monitoring/monitoring.module";
import {CaptureDeviceModule} from "../capture-device/capture-device.module";
import {MouseClickIndicationService} from "./mouse-click-indication.service";
import {RecordingUtilsService} from "./recording-utils.service";

@NgModule({
  imports: [
    InfraModule,
    MonitoringModule,
    CaptureDeviceModule
  ],
  providers: [
    RecordingDetailsService,
    RecordingRecoveryService,
    SlideService,
    MouseClickIndicationService,
    RecordingUtilsService
  ]
})
export class RecordingModule {}



