import {Injectable} from "@angular/core";
import {utils} from "../../../utils/utils";
import * as _ from "lodash";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {AppPersistentDataService} from "../infra/app-persistent-data.service";
import {AppLoggerService} from "../infra/app-logger.service";
import {AppStateService} from "../infra/app-state.service";
import {RecordingDetailsService} from "./recording-details.service";
import {AudioDeviceSettingsService} from "../capture-device/audio-device-settings.service";
import {VideoDeviceService} from "../capture-device/video-device.service";



export type engineFileObject = {
  id: string,
  path: string,
  slides?:string,
  state?:string,
  type?:string
}

@Injectable()
export class RecordingUtilsService {

  //mp4 files suffix
  readonly metadataFileSuffix = ".metadata";
  readonly mediatypesFileSuffix = ".mediatypes";

  constructor(
    private log: AppLoggerService,
    private persistencyService: AppPersistentDataService,
    private appState: AppStateService,
    private recordingDetailsService: RecordingDetailsService,
    private videoDeviceService: VideoDeviceService,
    private audioSettingsService: AudioDeviceSettingsService){}

  public getRecordingFilesForDeletion(filesFromEngine: engineFileObject[] = [])
  {
    let filesToDelete = [];
    _.forEach(filesFromEngine, (file) => {
      if(file) {
        filesToDelete.push(file.path);
        if (file.slides) {
          filesToDelete.push(file.slides);
        }
      }
    });
    // check that files for deletion got from the engine equals to the files we have in our persistent data
    // (inactive sessions files won't return from the engine)
    return this.getFilesToDeletionFromPersistency(filesToDelete);

  }

  public removeRecordedFiles(files)
  {
    this.log.info('removing recorded files: ' + JSON.stringify(files));
    return ipcWrapper.sendIPC('removeFiles', files)
      .catch((err) => {
        this.log.error('failed to remove recorded files: ' + JSON.stringify(files) + '. Error: ' + utils.stringifyMsg(err));
      });
  }

  /**
   * Using this function during cancel recording flow. since we don't know if the engine will return all relevant files
   * for deletion (disconnection for example) we are adding relevant files from our persistency file.
   * @param filesToDelete
   */
  private getFilesToDeletionFromPersistency(filesToDelete)
  {
    return this.persistencyService.get('recording.details.files')
      .then((files) => {
        let slidesFoldersPromises = [];
        if(files && !_.isEmpty(files)) {
          _.forEach(files, mp4File => {
            filesToDelete.push(mp4File);
            filesToDelete.push(mp4File + this.mediatypesFileSuffix);
            filesToDelete.push(mp4File + this.metadataFileSuffix);
            //in case there are some slides related to this session - remove them
            slidesFoldersPromises.push(this.getRecordingFileRelatedSlidesDir(mp4File.replace(/^.*[\\\/]/, '')));
          });
        }
        return Promise.all(slidesFoldersPromises);
      })
      .then((slidesFolders) => {
        _.forEach(slidesFolders, slideFolder => {
          filesToDelete.push(slideFolder);
        });
        return _.uniq(filesToDelete);
      })
      .catch((err) => {
        this.log.error('Failed to get recording files from our persistency file: ' + utils.stringifyMsg(err));
        return filesToDelete;
      });
  }

  private getRecordingFileRelatedSlidesDir(mp4File)
  {
    let slideDirName = mp4File.substr(0, mp4File.lastIndexOf('.'));
    return ipcWrapper.sendIPC('getSlidesFolderForCaptureFileName', slideDirName);
  }

  public isProcessing()
  { //will be ready only if all main sessions devices are in stable state
    let sessionsIds = _.keys(this.appState.getActiveSessions());
    let stable = true;
    _.forEach(sessionsIds,  (sessionId) => {
      stable = stable && this.appState.isSourceStable(sessionId, true);
    });
    return !stable;
  }

  public updateRecState(state)
  {
    this.log.info('update recording state to: ' + state);
    this.appState.updateAppState('rec-state', state);
  }

  public getSessionActionArgs(action, sessionId?): any
  {
    if (action === 'stopRecord' || action === 'recoverMP4File'){
      return this.recordingDetailsService.getMetaData(sessionId);
    }
    return Promise.resolve(); //no prams is needed
  }

  public finalizeRecordingFileOffCycle(files)
  {
    this.log.info('finalizeRecordingFileOffCycle()');
    let strAction;
    return this.getSessionActionArgs('recoverMP4File')
      .then((recordingActionArgs) => {
        let finalizeCalls = [];
        _.forEach(files, function (file) {
          finalizeCalls.push(ipcWrapper.sendIPC('recoverMP4File', file, ...recordingActionArgs));
        });
        strAction = JSON.stringify(this.appState.getActiveRecordableSessionsIds());
        this.appState.onCallStarted('recoverMP4File', strAction);

        return Promise.all(finalizeCalls);
      }).then((res) => {
        this.appState.onCallEnded('recoverMP4File - SUCCESS', strAction);
        return true;
      })
      .catch((err) => {
        this.appState.onCallEnded('recoverMP4File - FAILURE', strAction);
        return Promise.reject(err);
      });
  }

  public updateRecordingFiles(files)
  {
    _.forEach(files, file => {
      if(!_.isEmpty(this.appState.getActiveSessions()) && !_.isEmpty(this.appState.getActiveSessions()[file['id']])) {
        let sessionName = this.videoDeviceService.getSessionNameById(file['id']) ?
          this.videoDeviceService.getSessionNameById(file['id']) :
          this.audioSettingsService.getSessionNameById(file['id']) ;
        this.recordingDetailsService.setRecordedFilePath(sessionName, file['path']);
      }
    });
  }

}



